<template>
	<div class="wrap">
		<publicHeader></publicHeader>
		<nuxt-page :keepalive></nuxt-page>
		<publicFooter></publicFooter>
	</div>
</template>
<script setup>
import {useCheckMobile} from "@/store/isMobile";
const keepalive = {
        include: ["newsPage"],
};
const checkIsMobile = (()=>{
	useCheckMobile().setItem(window.innerWidth <= 1024)
})
onMounted(()=>{
	checkIsMobile()
	window.addEventListener('resize',()=>{
		checkIsMobile()
	})
})
onUnmounted(()=>{
	window.removeEventListener('resize',()=>{})
})
</script>
<style scoped lang="scss"></style>
